import React,{useEffect,useMemo} from 'react';
import { useCategory } from '../../contexts/CategoryContext';
import { useParams,useNavigate } from 'react-router-dom';
import { useOfflineMode } from '../../contexts/OfflineModeContext';
import useDownloadBookSongs from '../../hooks/useDownloadBookSongs';
import { useDownloadStatus } from '../../contexts/DownloadStatusContext';
import { useAuth } from '../../contexts/AuthContext';
import Breadcrumbs from './Breadcrumbs';


function BookList( ) {
  const { categoryId } = useParams<{ categoryId: string }>();
  const { categories, isLoading, error } = useCategory();
  const navigate = useNavigate();
  const { isEffectivelyOffline } = useOfflineMode();
  const { isDownloading:isGlobalDownloading, downloadAllSongsForBook,getDownloadedSongCount,isDownloadingBook } = useDownloadBookSongs();
  const { downloadStatus,updateDownloadProgress,updateDownloadedCount } = useDownloadStatus();
  //const [downloadingBookId, setDownloadingBookId] = useState<string | null>(null);
  //const [downloadedCounts, setDownloadedCounts] = useState<{ [bookId: string]: number }>({});
  const selectedCategory = categories?.find(cat => cat.id === categoryId);
  //const filteredBooks = isEffectivelyOffline ?  selectedCategory?.books?.filter(book => /* your offline filter logic */ true) : books; // Assuming you have this
  
  const { session } = useAuth();
  const userEmail = session?.user?.email;

  const filteredBooks = useMemo(() => {
    return selectedCategory?.books?.filter(book => {
      if (!isEffectivelyOffline) {
        return true;
      }
      return localStorage.getItem(`sankeertan_song_indices-${encodeURIComponent(book.bookId)}`);
    }) ?? []; // Provide a default empty array in case selectedCategory?.books is undefined
  }, [selectedCategory?.books, isEffectivelyOffline]);

  useEffect(() => {
    if (filteredBooks) {
      filteredBooks.forEach(book => {
        //const downloadedCount = getDownloadedSongCount(book.bookId); // Use the new method
        if (book.songCount) {
          updateDownloadProgress(book.bookId, book.songCount); // Initialize total count
          const downloadedCount = getDownloadedSongCount(book.bookId); // Get downloaded count
          updateDownloadedCount(book.bookId, downloadedCount); // Update downloaded count in context
        }
      });
    }
  }, [filteredBooks, getDownloadedSongCount, updateDownloadProgress, updateDownloadedCount]); 

  useEffect(() => {
    const initialCounts: { [bookId: string]: number } = {};
              
    filteredBooks?.forEach(book => {
      const keyPrefix = `sankeertan_song_indices-${encodeURIComponent(book.bookId)}`;
      const storedIndices = localStorage.getItem(keyPrefix);
                                              
      if (storedIndices) {
        try {
          const parsedData = JSON.parse(storedIndices);
          if (parsedData && Array.isArray(parsedData.data)) { // Access the 'data' property
            const indices: { songTitle: string; sourceRow: number }[] = parsedData.data;
            let downloaded = 0;
            indices.forEach(index => {
              const songKey = `sankeertan_song_details-${book.bookId}-${encodeURIComponent(index.songTitle)}`;
              if (localStorage.getItem(songKey)) {
                downloaded++;
              }
              });
            initialCounts[book.bookId] = downloaded;
          } else {
            console.warn(`Song indices for book ${book.bookId} are not in the expected format:`, parsedData);
            initialCounts[book.bookId] = 0;
          }
        } catch (error) {
          console.error(`Error parsing song indices for book ${book.bookId}:`, error);
        }
      } else {
        initialCounts[book.bookId] = 0;
      }
    });
  }, [filteredBooks]);
  if (isLoading) {
    return <div>Loading books...</div>;
  }

  if (error instanceof Error) {
    return <div>Error loading books: {error.message}</div>;
  }

  const handleDeleteIndexClick=async(event:any,bookId: string)=>{
    event?.stopPropagation(); 
    let confirmed=window.confirm("Pls confirm for deletion of book index ["+bookId+"]");
    if (! confirmed){
      return;
    }
    const prefix = "sankeertan_song_details-" + bookId; 
    const keysToRemove = [];

    let keyToRemove="sankeertan_song_indices-"+bookId
    localStorage.removeItem(keyToRemove);
    //navigate("/categories");
    return;
  }


  const handleDeleteBookClick=async(event:any,bookId: string)=>{
    event?.stopPropagation(); 
    let confirmed = window.confirm(`WARNING: This will permanently delete the ENTIRE book "${bookId}". This action cannot be undone. Please confirm.`);
    if (! confirmed){
      return;
    }
    const prefix = "sankeertan_song_details-" + bookId; 
    const keysToRemove = [];

    for (let i = 0; i < localStorage.length; i++) {
      const key = localStorage.key(i);
      if (key && key.startsWith(prefix)) {
        keysToRemove.push(key);
      }
    }
    keysToRemove.forEach(key => {
      localStorage.removeItem(key);
    });

    let keyToRemove="sankeertan_song_indices-"+bookId
    localStorage.removeItem(keyToRemove);
    //navigate("/categories");
    return;
  }
  
  const handleBookClick = (event:any,bookId: string) => {
    event?.stopPropagation();
    // Navigate to the book details page using the bookId
    navigate(`/book/${categoryId}/${bookId}`);
  };

  const handleDownloadClick = async (event:any,bookId: string, totalSongs: number) => {
    //localStorage.setItem(`downloading-${encodeURIComponent(bookId)}`, 'true');
    event?.stopPropagation();
    await downloadAllSongsForBook(bookId,userEmail); 
    //localStorage.removeItem(`downloading-${encodeURIComponent(bookId)}`);
  };

const DownloadIcon = (
  <svg xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24" fill="currentColor">
    <path d="M0 0h24v24H0z" fill="none" />
    <path d="M19 9h-4V3H9v6H5l7 7 7-7zM5 18h14v2H5v-2z" />
  </svg>
);

const DownloadedIcon = (
  <svg xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24" fill="currentColor">
    <path d="M0 0h24v24H0z" fill="none"/>
    <path d="M9 16.17L4.83 12l-1.42 1.41L9 19 21 7l-1.41-1.41z"/>
  </svg>
);

const DeleteBookIcon = (
  <svg xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24" fill="currentColor">
    <path d="M0 0h24v24H0z" fill="none" />
    <path d="M6 19c0 1.1.9 2 2 2h8c1.1 0 2-.9 2-2V7H6v12zM19 4h-3.5l-1-1h-5l-1 1H5v2h14V4z" />
  </svg>
);

const DeleteIndexIcon = (
  <svg xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 0 24 24" width="24" fill="currentColor">
    <path d="M0 0h24v24H0z" fill="none" />
    <path d="M14 2H6c-1.1 0-1.99.9-1.99 2L4 20c0 1.1.89 2 1.99 2H18c1.1 0 2-.9 2-2V8l-6-6zM6 4h7v5h5v11H6V4zm-2 16h16V4h-3V1h-4v3h-3v-3H4v3H1v2h3v12zM12 9l-4 4h3v4h2v-4h3l-4-4z" />
  </svg>
);

  return (
  <>
    <Breadcrumbs />
    <div>
      {filteredBooks ? (
        <ul className="list-none p-0">
          {filteredBooks.map((book) => {
            const downloadedSongs = downloadStatus[book.bookId]?.downloadedSongs || 0;
            const totalSongs = book.songCount || 0;
            const percentage = totalSongs > 0 ? (downloadedSongs / totalSongs) * 100 : 0;
            const isFullyDownloaded = getDownloadedSongCount(book.bookId) === (book.songCount || 0);
            //const isIndexDownloaded = localStorage.getItem(`sankeertan_song_indices-${encodeURIComponent(book.bookId)}`);
            const isIndexDownloaded = !!localStorage.getItem(`sankeertan_song_indices-${encodeURIComponent(book.bookId)}`);
            console.log(`isIndexDownloaded:${isIndexDownloaded}`)

            return (
              <li
                key={book.bookId}
                onClick={(e) => handleBookClick(e, book.bookId)}
                className="relative overflow-hidden cursor-pointer border-b border-gray-300 py-2 px-4"
              >
                <div className="z-20 relative grid grid-cols-[1fr_auto] gap-4 items-center divide-x divide-gray-300">

                  {/* Book Name column: now the parent for the progress bar */}
                  <div className="relative text-lg font-medium pr-4">
                      
                    {/* Progress Bar (Unified Color) - These are now scoped to this div */}
                    <div 
                      className="absolute inset-0 bg-gray-200 z-0 rounded-md" 
                      style={{ width: `100%` }}>
                    </div>
                    <div 
                      className="absolute inset-0 bg-blue-500 z-10 rounded-md" 
                      style={{ width: `${percentage}%` }}>
                    </div>

                    {/* The text needs to be on top of the progress bar */}
                    <span className={`relative z-20 ${percentage > 25 ? 'text-white' : 'text-gray-800'}`}>
                      {book.bookName}
                    </span>
                  </div>

                  {/* Right side: All Status and Actions */}
                  <div className="flex items-center space-x-4 divide-x divide-gray-300 pl-4">

                    {/* Download Status */}
                    <div className="flex items-center text-sm text-gray-600 pr-4 min-w-[150px]">
                      <span>
                        ({downloadedSongs}/{totalSongs} downloaded)
                      </span>
                    </div>

                    {/* Download/Downloaded Status */}
                    <div className="flex items-center pl-4 min-w-[120px]">
                      {!isFullyDownloaded ? (
                        <button
                          onClick={(e) => { e.stopPropagation(); handleDownloadClick(e, book.bookId, book.songCount ?? 0); }}
                          disabled={isEffectivelyOffline || isGlobalDownloading || isDownloadingBook(book.bookId)}
                          className="flex items-center text-blue-600"
                        >
                          {isDownloadingBook(book.bookId) ? (
                            'Downloading...'
                          ) : (
                            <>
                              {DownloadIcon} <span className="ml-1">Download</span>
                            </>
                          )}
                        </button>
                      ) : (
                        <span className="flex items-center text-green-500">
                          {DownloadedIcon} <span className="ml-1">Done</span>
                        </span>
                      )}
                    </div>

                    {/* Action Buttons */}
                    <div className="flex items-center space-x-2 pl-4">
                      <button
                        onClick={(e) => { e.stopPropagation(); handleDeleteIndexClick(e, book.bookId); }}
                        disabled={!isIndexDownloaded}
                        className="flex items-center text-red-500 border border-red-500 px-2 py-1 rounded-md hover:bg-red-50 disabled:opacity-50 disabled:cursor-not-allowed disabled:bg-gray-400 disabled:text-gray-600 disabled:border-gray-400"
                      >
                        {DeleteIndexIcon} <span className="ml-1">Index</span>
                      </button>
                      <button
                        onClick={(e) => { e.stopPropagation(); handleDeleteBookClick(e, book.bookId); }}
                        disabled={isGlobalDownloading || isDownloadingBook(book.bookId) || percentage === 0}
                        className="flex items-center bg-red-500 text-white border border-red-500 px-2 py-1 rounded-md hover:bg-red-600 disabled:opacity-50 disabled:cursor-not-allowed disabled:bg-gray-400 disabled:text-gray-600 disabled:border-gray-400s"
                      >
                        {DeleteBookIcon} <span className="ml-1">Book</span>
                      </button>
                    </div>
                  </div>
                </div>
              </li>
            );
          })}
        </ul>
      ) : (
        <p>No books found in this category.</p>
      )}
    </div>
  </>
);
}

export default BookList;