import React, { useState, useCallback, useRef } from 'react';
import JSZip from 'jszip'; // Import the jszip library
import {REACT_APP_SUPABASE_PROJECTID} from "../../../utils/constants";

interface FileUploadResult {
    success: boolean;
    message: string;
}

const LocalStorageSyncPage = () => {
    const [isProcessing, setIsProcessing] = useState(false);
    const [uploadResults, setUploadResults] = useState<FileUploadResult[]>([]);
    const fileInputRef = useRef<HTMLInputElement>(null);

    const handleFileUpload = useCallback(async (
        files: FileList | null,
    ): Promise<void> => {
        if (!files || files.length === 0) {
            setUploadResults(prevResults => [...prevResults, {
                success: false,
                message: 'No files selected.',
            }]);
            return;
        }

        setIsProcessing(true);
        const newUploadResults: FileUploadResult[] = [];

        try {
            for (let i = 0; i < files.length; i++) {
                const file = files[i];

                if (file.type === 'application/zip' || file.name.endsWith('.zip')) {
                    // Handle ZIP file
                    try {
                        const zip = await JSZip.loadAsync(file);
                        const zipEntries = Object.values(zip.files);

                        for (const entry of zipEntries) {
                            if (!entry.dir && entry.name.endsWith('.json')) {
                                // Process only JSON files within the ZIP
                                try {
                                    const content = await entry.async('string');
                                    const jsonData = JSON.parse(content);
                                    const filename = entry.name.split('/').pop() || 'unknown.json'; // Get filename
                                    const fileKey = filename.replace(/\.json$/, ''); // Use filename
                                    localStorage.setItem(fileKey, JSON.stringify(jsonData));
                                    newUploadResults.push({
                                        success: true,
                                        message: `Successfully extracted and stored "${filename}" with key "${fileKey}" from ZIP.`,
                                    });
                                } catch (e: any) {
                                    const errorMessage = e instanceof Error ? e.message : 'Unknown error';
                                    newUploadResults.push({
                                        success: false,
                                        message: `Error processing file "${entry.name}" from ZIP: ${errorMessage}`,
                                    });
                                }
                            }
                            else if (!entry.dir) {
                                newUploadResults.push({
                                    success: false,
                                    message: `Skipped non-JSON file "${entry.name}" from ZIP.`,
                                });
                            }
                        }
                    } catch (zipError: any) {
                        const zipErrorMessage = zipError instanceof Error ? zipError.message : 'Invalid ZIP file';
                        newUploadResults.push({
                            success: false,
                            message: `Error processing ZIP file "${file.name}": ${zipErrorMessage}`,
                        });
                    }
                } else if (file.type === 'application/json' || file.name.endsWith('.json')) {
                    // Handle individual JSON file
                    try {
                        const reader = new FileReader();
                        const content = await new Promise<string>((resolve, reject) => {
                            reader.onload = () => resolve(reader.result as string);
                            reader.onerror = reject;
                            reader.readAsText(file);
                        });
                        const jsonData = JSON.parse(content);
                        const filename = file.name;
                        const fileKey = filename.replace(/\.json$/, '');
                        localStorage.setItem(fileKey, JSON.stringify(jsonData));
                        newUploadResults.push({
                            success: true,
                            message: `Successfully stored "${file.name}" with key "${fileKey}".`,
                        });
                    } catch (e: any) {
                        const errorMessage = e instanceof Error ? e.message : 'Unknown error';
                        newUploadResults.push({
                            success: false,
                            message: `Error processing file "${file.name}": ${errorMessage}`,
                        });
                    }
                }
                else {
                    newUploadResults.push({
                        success: false,
                        message: `File "${file.name}" is not a JSON or ZIP file. Skipped.`,
                    });
                }
            }
        } finally {
            setUploadResults(prevResults => [...prevResults, ...newUploadResults]);
            setIsProcessing(false);
        }
    }, []);

    const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        handleFileUpload(event.target.files);
        // Reset the input so you can upload the same files again
        if (fileInputRef.current) {
            fileInputRef.current.value = '';
        }
    };

    const clearUploadResults = () => {
        setUploadResults([]);
    };

    const handleExport = useCallback(async () => {
        setIsProcessing(true);
        try {
            debugger;
            const zip = new JSZip();
            let hasData = false; //track if there is data to zip
            let categoriesData: any = null;

            // 1.  Get sankeertan_categories_data.json first
            const categoriesJson = localStorage.getItem('sankeertan_categories_data');
            if (categoriesJson) {
                try {
                    categoriesData = JSON.parse(categoriesJson);
                    zip.file('sankeertan_categories_data.json', categoriesJson); //add to zip
                    hasData = true;
                } catch (e) {
                    console.warn('sankeertan_categories_data.json is not valid JSON, skipping.');
                    categoriesData = null; // Ensure it's null if parsing fails
                }
            } else {
                console.warn('sankeertan_categories_data.json not found in localStorage.');
            }

            const bookFolders: { [bookName: string]: JSZip } = {}; // To store folders

            // 2. Process the rest of the keys
            for (let i = 0; i < localStorage.length; i++) {
                debugger;
                const key = localStorage.key(i);
                console.log(key);
                if(key === 'sb-'+REACT_APP_SUPABASE_PROJECTID+'-auth-token'){
                    continue; // do not export this key

                }
                if (key && key !== 'sankeertan_categories_data' ) { // Skip categories and authtoken key
                    const value = localStorage.getItem(key);
                    if (value) {
                        try {
                            JSON.parse(value); //validate json
                            let targetFolder: JSZip = zip; // Default: root folder
                            if (categoriesData && Array.isArray(categoriesData.data)) {
                                for (const category of categoriesData.data) {
                                    for(const book of category.books){
                                        const bookName = book.bookId;
                                        const songDetailsPrefix = `sankeertan_song_details-${bookName.toLowerCase().replace(/ /g, '-')}`;
                                        const songIndicesPrefix = `sankeertan_song_indices-${bookName.toLowerCase().replace(/ /g, '-')}`;
                                        //console.log(`songIndicesPrefix:${songIndicesPrefix}`)
                                        if (key.startsWith(songDetailsPrefix) || key.startsWith(songIndicesPrefix)) {
                                            if (!bookFolders[bookName]) {
                                                const newFolder = zip.folder(bookName);
                                                if (newFolder) {  // Check if folder creation was successful
                                                    bookFolders[bookName] = newFolder;
                                                }
                                                else {
                                                    bookFolders[bookName] = zip;
                                                }
                                            }
                                            targetFolder = bookFolders[bookName] || zip;
                                            break; // Exit the loop, found the book
                                        }
                                    }
                                }
                            }
                            targetFolder.file(`${key}.json`, value);
                            hasData = true;

                        } catch (e) {
                            console.warn(`Skipping key "${key}" as value is not valid JSON`);
                        }
                    }
                }
            }

            if (!hasData) {
                setUploadResults(prevResults => [...prevResults, { success: false, message: 'No valid JSON data found in localStorage to export.' }]);
                setIsProcessing(false);
                return;
            }

            const blob = await zip.generateAsync({ type: 'blob', mimeType: 'application/zip', compression: 'DEFLATE' });

            // Use a standard approach for downloading
            const url = URL.createObjectURL(blob);
            const a = document.createElement('a');
            a.href = url;
            a.download = 'sankeertan_localStorage_backup.zip';
            document.body.appendChild(a);
            a.click();
            document.body.removeChild(a);
            URL.revokeObjectURL(url);

            setUploadResults(prevResults => [...prevResults, { success: true, message: 'Successfully exported localStorage to ZIP file with folder structure.' }]);

        } catch (error: any) {
            const errorMessage = error instanceof Error ? error.message : 'Unknown error during export';
            setUploadResults(prevResults => [...prevResults, { success: false, message: `Error exporting localStorage: ${errorMessage}` }]);
        } finally {
            setIsProcessing(false);
        }
    }, []);

    return (
        <div className="p-4">
            <h1 className="text-2xl font-bold mb-4">Local Storage Sync</h1>

            <div className="mb-4">
                <input
                    type="file"
                    accept=".json,.zip"
                    onChange={handleFileChange}
                    disabled={isProcessing}
                    aria-label="Upload JSON or ZIP Files"
                    multiple
                    ref={fileInputRef}
                />
                <p className="text-sm text-gray-500 mt-1">
                    Upload JSON files or a ZIP file containing JSON files.  Filename will be used as the key.
                </p>
            </div>

            <div className="mb-4">
                <button
                    onClick={handleExport}
                    disabled={isProcessing}
                    className="px-4 py-2 border rounded-md bg-blue-500 text-white" // Basic styling
                >
                    {isProcessing ? 'Exporting...' : 'Export to ZIP'}
                </button>
            </div>

            {isProcessing && (
                <div className="mb-4 flex items-center gap-2">
                    <span>{isProcessing ? 'Processing...' : 'Exporting...'}</span>
                </div>
            )}

            {uploadResults.length > 0 && (
                <div className="mb-4">
                    <h2 className="text-lg font-semibold mb-2">Upload Results:</h2>
                    {uploadResults.map((result, index) => (
                        <div  // Replaced Alert with a div
                            key={index}
                            className={`mb-2 p-3 border rounded-md ${result.success
                                ? "border-green-500 bg-green-100 text-green-800"
                                : "border-red-500 bg-red-100 text-red-800"
                            }`}
                        >
                            {result.success ? (
                                <span style={{ color: 'green' }}>✓</span>
                            ) : (
                                <span style={{ color: 'red' }}>✗</span>
                            )}
                            <strong className="mr-1">
                                {result.success ? 'Success' : 'Error'}
                            </strong>
                            <span>
                                {result.message}
                            </span>
                        </div>
                    ))}
                    <button  // Changed to a standard button
                        onClick={clearUploadResults}
                        className="mt-2 px-4 py-2 border rounded-md" // Basic styling
                    >
                        Clear Results
                    </button>
                </div>
            )}
            <div className="mt-8 p-4 border rounded-md bg-gray-50">
                <h3 className="text-lg font-semibold mb-2">Important Notes:</h3>
                <ul className="list-disc list-inside space-y-2 text-gray-700">
                    <li>
                        <span className="font-semibold">ZIP File Handling:</span>  This page now supports uploading a single ZIP file.  The application will extract JSON files from the ZIP and store them in localStorage.
                    </li>
                    <li>
                        <span className="font-semibold">Key Derivation:</span> The filename of each JSON file is used as the key for storing its content in localStorage.
                    </li>
                    <li>
                        <span className="font-semibold">File Overwriting:</span> If a key already exists in localStorage, it will be overwritten with the new content.
                    </li>
                    <li>
                        <span className="font-semibold">ZIP Structure:</span> The ZIP file can contain JSON files in subfolders.  The filename of the file within the ZIP will be used as the localStorage key.
                    </li>
                    <li>
                        <span className="font-semibold">Non-JSON Files in ZIP:</span> Any non-JSON files within the ZIP will be skipped.
                    </li>
                    <li>
                        <span className="font-semibold">Error Handling:</span> The application attempts to provide informative error messages for file processing issues, including problems with individual files within a ZIP archive.
                    </li>
                    <li>
                        <span className="font-semibold">Export Feature:</span>  A new "Export to ZIP" button has been added.  Clicking this will create a ZIP file containing all key-value pairs from your browser's localStorage (where the values are valid JSON), and prompt you to download it.  The keys will be used as the filenames in the ZIP.
                    </li>
                    <li>
                        <span className="font-semibold">Exported Data Format:</span> The exported ZIP file will now contain a folder structure.
                    </li>
                    <li>
                        <span className="font-semibold">Book Folders:</span>  Data is organized into folders based on the book names found in  `sankeertan_categories_data.json`.
                    </li>
                    <li>
                         <span className="font-semibold">Root Folder:</span> Any keys that do not match the book naming convention will be placed in the root of the ZIP file.
                    </li>
                    <li>
                        <span className="font-semibold">sankeertan_categories_data.json:</span>  This file is included in the root of the ZIP.
                    </li>
                </ul>
            </div>
        </div>
    );
};

export default LocalStorageSyncPage;
