export const broadcastContent = async function (content: string) {
  try {
    //const client=getsbclient()
    await supabase
      .from("sankeertan")
      .upsert({ id: 1, content: content, scroll_position: 0 });
    return true;
  } catch (e) {
    console.error(e);
    return false;
  }
};

const fetchSongDetails = async ({
  bookId,
  sourceRow,
  songTitle,
  sessionuser
}: {
  bookId: string;
  sourceRow: string;
  songTitle: string;
  sessionuser:string |null;
}): Promise<any[]> => {
  if (!bookId || !sourceRow) {
    throw new Error(
      "bookId or sourceRow is not defined. Cannot fetch songDetails"
    );
  }
  
  const url = new URL(GOOGLE_WEBAPP_URL);
  url.searchParams.append('mode', 'api');
  url.searchParams.append('apiname', 'getsongtextforsong');
  url.searchParams.append('book', bookId);
  url.searchParams.append('sourcerow', sourceRow);
  url.searchParams.append('songtitle', songTitle);
  // Only add the sessionuser if it exists
  if (sessionuser) {
    url.searchParams.append('sessionuser', sessionuser);
  }

   const response = await fetch(url.toString());  
  if (!response.ok) {
    const errorData = await response.json();
    throw new Error(
      `Failed to fetch song details for book ${bookId}+row ${sourceRow}: ${
        response.status
      } - ${JSON.stringify(errorData)}`
    );
  }
  return await response.json();
};

