  function extractTableTextFromHtmlOld1(htmlString: string): string {
  const parser = new DOMParser();
  const doc = parser.parseFromString(htmlString, "text/html");
  const rows = doc.querySelectorAll("tr");
  let extractedText = "";

  rows.forEach((row) => {
    const cells = row.querySelectorAll("td, th");
    const rowTexts: string[] = [];

    cells.forEach((cell) => {
      // Create a temporary div to manipulate the content
      const tempDiv = document.createElement("div");
      tempDiv.innerHTML = cell.innerHTML;

      // Replace <p> tags with a newline, handling both open and closing tags
      const paragraphs = tempDiv.querySelectorAll("p");
      paragraphs.forEach((p) => {
        // Replace the <p> with its text content followed by a newline
        const pText = p.textContent || '';
        const newLine = '\n';
        p.outerHTML = pText + newLine;
      });

      // Replace <br> tags with a newline
      const cleanedText = tempDiv.innerHTML.replace(/<br\s*\/?>/gi, "\n");

      // Use textContent to get the final text, which handles any remaining HTML
      tempDiv.innerHTML = cleanedText;
      const cellText = tempDiv.textContent?.trim() || "";
      rowTexts.push(cellText);
    });

    extractedText += rowTexts.join(" | ") + "\n\n";
  });

  return extractedText.trim();
}

function extractTableTextFromHtml(htmlString: string): string {
  const parser = new DOMParser();
  const doc = parser.parseFromString(htmlString, "text/html");
  const rows = doc.querySelectorAll("tr");
  let extractedText = "";

  rows.forEach((row) => {
    const cells = row.querySelectorAll("td, th");
    const rowTexts: string[] = [];

    cells.forEach((cell) => {
      let cellHtml = cell.innerHTML;
      
      // Replace all <p> and </p> tags with a single newline
      // This handles cases like <p>Text</p><p>More Text</p>
      cellHtml = cellHtml.replace(/<\/p>\s*<p>/gi, "\n");
      
      // Replace opening <p> tags and any closing </p> tags with nothing,
      // as the text content is handled later.
      cellHtml = cellHtml.replace(/<p>|<\/p>/gi, "");
      
      // Replace <br> tags with a newline
      cellHtml = cellHtml.replace(/<br\s*\/?>/gi, "\n");

      // Create a temporary element to get the text content and handle HTML entities
      const tempDiv = document.createElement("div");
      tempDiv.innerHTML = cellHtml;
      const cellText = tempDiv.textContent?.trim() || "";
      
      rowTexts.push(cellText);
    });

    extractedText += rowTexts.join(" | ") + "\n\n";
  });

  return extractedText.trim();
}

