import React, { createContext,  useContext, useEffect, useMemo } from 'react';
import { useQuery } from '@tanstack/react-query';
import {GOOGLE_WEBAPP_URL} from '../utils/constants';
import {RawBookItemFromApi, CategoryItem} from '../interfaces/book'
import { useAuth } from '../contexts/AuthContext';


interface CategoryContextType {
  categories: CategoryItem[]; // Adjust the type based on your category data structure
  isLoading: boolean;
  error: Error | null;
}

const CategoryContext = createContext<CategoryContextType>({
  categories: [],
  isLoading: false,
  error: null,
});

const LOCAL_STORAGE_KEY = 'sankeertan_categories_data';
//const CACHE_EXPIRATION_TIME = 24 * 60 * 60 * 1000; // 24 hours in milliseconds

const fetchCategoriesAndBooks = async (userEmail ?: string | null): Promise<CategoryItem[]> => {
  const url = new URL(`${GOOGLE_WEBAPP_URL}`);
  url.searchParams.append('mode', 'api');
  url.searchParams.append('apiname', 'getcategoriesandbooks');
  url.searchParams.append('sessionuser', userEmail  ?? 'dummyfornow');

  const response = await fetch(url.toString());
  
    if (!response.ok) {
      const errorData = await response.json();
      throw new Error(`Failed to fetch categories and books: ${response.status} - ${JSON.stringify(errorData)}`);
    }
  
    const data = await response.json();
  
    const transformedCategories: CategoryItem[] = Object.keys(data)
    .filter(key => key !== 'books') // Still assuming 'books' is a top-level key to ignore for categories
    .map(key => ({
      name: key,
      bookCount: Array.isArray(data[key]) ? data[key].length : 0,
      books: Array.isArray(data[key]) ? data[key].map((book:RawBookItemFromApi) => ({
        bookId: book["Book Id"],
        bookName: book["Book Name"],
        songCount: book["Song Count"],
        verseCount: book["Verse Count"],
        isBookSourceSupabase: book["isBookSourceSupabase"] === "true" || book["isBookSourceSupabase"] === true || false, // Handle boolean or string
      })) : [],
      id: key.toLowerCase().replace(/ /g, '-'),
      image: `https://placehold.co/800/orange/black?text=${encodeURIComponent(key)}&font=lora`,
    }));
  
    return transformedCategories;
  };

export const CategoryProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const { session } = useAuth();
  const userEmail = session?.user?.email;

    const initialCategories = useMemo(() => {
        try {
            const storedData = localStorage.getItem(LOCAL_STORAGE_KEY);
            if (storedData) {
            const parsedData = JSON.parse(storedData);
            if (Array.isArray(parsedData?.data)) {
                return parsedData.data as CategoryItem[];
            }
            }
        } catch (error) {
            console.error('Error retrieving categories data from localStorage:', error);
        }
        return undefined; // Return undefined if no valid data in localStorage
        }, []);

  const { isLoading, error, data: fetchedCategories,isFetching  } = useQuery<CategoryItem[]>({
    queryKey: ['categories',userEmail],
    queryFn: () => fetchCategoriesAndBooks(userEmail ?? null),
    staleTime: Infinity, // Data should not be considered stale
    initialData: initialCategories,
    refetchOnMount: false,
    enabled: !!userEmail,
    //cacheTime: Infinity, // Keep data in cache indefinitely
  });


  useEffect(() => {
    if (fetchedCategories && !isFetching) {
      try {
        const dataToStore = {
          data: fetchedCategories,
          timestamp: Date.now(),
        };
        localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(dataToStore));
      } catch (error) {
        console.error('Error saving categories data to localStorage:', error);
      }
    }
  }, [fetchedCategories,isFetching]);



  const isLoadingCombined = isLoading || (initialCategories !== undefined && fetchedCategories === undefined && isFetching);


  return (
    <CategoryContext.Provider value={{ categories:fetchedCategories ?? [], isLoading: isLoadingCombined, error }}>
      {children}
    </CategoryContext.Provider>
  );
};

export const useCategory = () => useContext(CategoryContext);