import React from 'react';
import { useLocation, useParams, Link } from 'react-router-dom';
const breadcrumbStyles = {
  container: {
    fontFamily: 'sans-serif',
    fontSize: '14px',
    color: '#555',
    marginBottom: '20px',
  },
  link: {
    color: '#007bff',
    textDecoration: 'none',
    transition: 'color 0.2s ease-in-out',
  },
  separator: {
    margin: '0 5px',
    color: '#999',
  },
  currentPage: {
    fontWeight: 'bold',
    color: '#333',
    cursor: 'default',
    textDecoration: 'none',
  },
};  
interface BreadcrumbsProps {}

const Breadcrumbs: React.FC<BreadcrumbsProps> = () => {
  const location = useLocation();
  const { categoryId, bookId, songTitle } = useParams<{
    categoryId?: string;
    bookId?: string;
    songTitle?: string;
  }>();

  const decodedSongTitle = songTitle ? decodeURIComponent(songTitle) : '';

  const [isHovered, setIsHovered] = React.useState(false);
  const linkHoverStyle = {
    ...breadcrumbStyles.link,
    color: isHovered ? '#0056b3' : '#007bff',
    textDecoration: isHovered ? 'underline' : 'none',
  };

  const homeLink = (
    <Link 
      to="/" 
      style={linkHoverStyle}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
    >
      Home
    </Link>
  );
 
  if (location.pathname.startsWith('/books/')) {
    return (
      <div style={breadcrumbStyles.container}>
        {homeLink}
        <span style={breadcrumbStyles.separator}>&gt;</span>
        {categoryId}
      </div>
    );
  } else if (location.pathname.startsWith('/book/')) {
    // Note: To get the categoryId here, you would need to pass it as state
    // when navigating from the BookList, or perform a data fetch.
    return (
      <div style={breadcrumbStyles.container}>
        {homeLink}
        <span style={breadcrumbStyles.separator}>&gt;</span>
        <Link 
          to={`/books/${categoryId}`} 
          style={linkHoverStyle}
          onMouseEnter={() => setIsHovered(true)}
          onMouseLeave={() => setIsHovered(false)}
        >
          {categoryId}
        </Link>
        <span style={breadcrumbStyles.separator}>&gt;</span>
        {bookId}
      </div>
    );
  } else if (location.pathname.startsWith('/details/book/')) {
    // Note: The categoryId here is also needed and is not available in useParams.
    return (
      <div style={breadcrumbStyles.container}>
        {homeLink}
        <span style={breadcrumbStyles.separator}>&gt;</span>
        <Link 
          to={`/books/${categoryId}`} 
          style={linkHoverStyle}
          onMouseEnter={() => setIsHovered(true)}
          onMouseLeave={() => setIsHovered(false)}
        >
          {categoryId}
        </Link>      
        <span style={breadcrumbStyles.separator}>&gt;</span>
        <Link 
          to={`/book/${categoryId}/${bookId}`} 
          style={linkHoverStyle}
          onMouseEnter={() => setIsHovered(true)}
          onMouseLeave={() => setIsHovered(false)}
        >
          {bookId}
        </Link>
        <span style={breadcrumbStyles.separator}>&gt;</span>
        <span style={breadcrumbStyles.currentPage}>{decodedSongTitle}</span>
      </div>
    );
  }

  return null;
};

export default Breadcrumbs;