import React from "react";
import { Routes, Route, Navigate } from "react-router-dom";
import Login from "./components/UMS/Login";
import Signup from "./components/UMS/Signup";
import ChangePassword from "./components/UMS/ChangePassword";
import AuthHandler from "./components/UMS/AuthHandler";
import CategoryList from "./components/AppObjs/CategoryList";
import BookList from "./components/AppObjs/BookListX";
import SongIndexList from "./components/AppObjs/SongIndexList";
import SongDetailView from "./components/AppObjs/SongDetailView";
import SankeertanDisplay from "./components/AppObjs/ReadingTogether/SankeertanDisplay";
import { BookProvider } from "./contexts/BookContext";
//import { SongIndicesProvider } from "./contexts/SongIndicesContext";
import { useAuth } from "./contexts/AuthContext";
import { DownloadStatusProvider } from "./contexts/DownloadStatusContext";
import { LanguagePreferenceProvider } from "./contexts/LanguagePreferenceContext";
import LocalStorageSyncPage from "./components/AppObjs/LocalStorageAdmin/LocalStorageSyncPage";
import Settings from "./components/AppObjs/Settings";
import ProtectedRoute from './ProtectedRoute';

function AppRoutes() {
  const { session, isLoading } = useAuth();
  // Conditionally render routes based on authentication state
  if (isLoading) {
    return <div>Checking authentication...</div>; // Or a loading spinner
  }

  return (
    <LanguagePreferenceProvider>
      <DownloadStatusProvider>
        <Routes>
          <Route path="/login" element={<Login />} />
          <Route path="/signup" element={<Signup />} />
          <Route path="/change-password" element={<ChangePassword />} />
          <Route path="/auth/callback" element={<AuthHandler />} />{" "}
          {/* Handle the callback URL */}
          {/* Protected routes */}
          <Route
            path="/settings"
            element={session ? <Settings /> : <Navigate to="/login" replace />}
          />
          {/*<Route path="/categories" element={session ? <CategoryList /> : <Navigate to="/login" replace />} />*/}
          <Route
            path="/categories"
            element={
             <ProtectedRoute session={session}>
                  <CategoryList />
             </ProtectedRoute>
            }
          />
          <Route
            path="/books/:categoryId"
            element={
              <ProtectedRoute session={session}>
                <BookProvider>
                  <BookList />
                </BookProvider>
              </ProtectedRoute>  
            }
          />
          <Route
            path="/book/:categoryId/:bookId"
            element={
              <ProtectedRoute session={session}>
                <SongIndexList />
              </ProtectedRoute>
            }
          />
          <Route
            path="/details/book/:categoryId/:bookId/:sourceRow/:songTitle"
            element={
              <ProtectedRoute session={session}>
                <SongDetailView />
              </ProtectedRoute>
            }
          />
          <Route
            path="/sankeertan/:broadcastId"
            element={
               <ProtectedRoute session={session}>
                  <SankeertanDisplay broadcastId={1} />
                </ProtectedRoute>
            }
          />
          <Route
            path="/localstoragesync"
            element={
              <ProtectedRoute session={session}>
                <LocalStorageSyncPage />
              </ProtectedRoute>  
            }
          />
          <Route
            path="/"
            element={
              session ? (
                <Navigate to="/categories" replace />
              ) : (
                <div>Welcome!</div>
              )
            }
          />
        </Routes>
      </DownloadStatusProvider>
    </LanguagePreferenceProvider>
  );
}

export default AppRoutes;
