import { useState, useEffect } from 'react';

/**
 * A custom React hook that checks if a CSS media query is matched.
 *
 * @param query The media query string (e.g., '(max-width: 600px)').
 * @returns A boolean indicating whether the query currently matches.
 */
const useMediaQuery = (query: string): boolean => {
  const [matches, setMatches] = useState<boolean>(false);

  useEffect(() => {
    // Check if window and matchMedia API exist (for server-side rendering safety)
    if (typeof window === 'undefined' || !window.matchMedia) {
      return;
    }

    const mediaQueryList = window.matchMedia(query);
    const listener = (event: MediaQueryListEvent) => setMatches(event.matches);

    // Set initial state
    setMatches(mediaQueryList.matches);

    // Add and remove event listener for changes
    mediaQueryList.addEventListener('change', listener);

    // Cleanup function to remove the listener
    return () => {
      mediaQueryList.removeEventListener('change', listener);
    };
  }, [query]);

  return matches;
};

export default useMediaQuery;