import React, { ReactNode } from 'react';
// @ts-ignore
import { Navigate, useLocation } from 'react-router-dom';

// Define the types for the props
interface ProtectedRouteProps {
  children: ReactNode;
  session: any; // Use a more specific type if you know the session object's structure
}

const ProtectedRoute = ({ children, session }: ProtectedRouteProps) => {
  const location = useLocation();

  if (!session) {
    // If the user is not authenticated, save the current path
    // in session storage before redirecting to the login page.
    sessionStorage.setItem('redirect_path', location.pathname);
    return <Navigate to="/login" replace />;
  }
  // If the user is authenticated, render the child component.
  return <>{children}</>;
};

export default ProtectedRoute;