import React, { useState,useEffect } from 'react';
//import { useBook } from '../../contexts/BookContext';
import { useNavigate } from 'react-router-dom';
import { useCategory } from '../../contexts/CategoryContext';
import useMediaQuery from './useMediaQuery'; 

function CategoryList() {
  const { categories,isLoading, error } = useCategory();
  const navigate = useNavigate();
  //const [selectedCategory, setSelectedCategory] = useState(null);
  const [categoryColors, setCategoryColors] = useState([]);

  const isMobilePortrait = useMediaQuery('(max-width: 600px) and (orientation: portrait)');
  const isMobileLandscape = useMediaQuery('(max-width: 900px) and (orientation: landscape)');

  
  // Generate distinct colors for each category
  useEffect(() => {
    const generateDistinctColors:any = (count:number) => {
      const colors = [];
      const hueStep = 360 / count;
      
      for (let i = 0; i < count; i++) {
        const hue = i * hueStep;
        colors.push(`hsl(${hue}, 70%, 60%)`);
      }
      
      return colors;
    };

    setCategoryColors(generateDistinctColors(categories.length));
  }, [categories.length]);


  if (isLoading) {
    return <div>Loading categories...</div>;
  }

  if (error instanceof Error) {
    return <div>Error loading categories: {error.message}</div>;
  }
  
  const handleCategorySelect = (e:React.MouseEvent<HTMLDivElement>,categoryId: string) => {
    e.stopPropagation();
    navigate(`/books/${categoryId}`);
  };

   const styles:{[key:string]:React.CSSProperties } = {
    container: {
      maxWidth: '1000px',
      margin: '0 auto',
      padding: '20px',
      fontFamily: "'Segoe UI', Tahoma, Geneva, Verdana, sans-serif",
      backgroundColor: '#f5f7fa',
      borderRadius: '12px',
      boxShadow: '0 4px 20px rgba(0, 0, 0, 0.1)'
    },
    heading: {
      textAlign: 'center',
      color: '#2c3e50',
      marginBottom: '25px',
      fontSize: '32px',
      fontWeight: '700',
      paddingBottom: '15px',
      borderBottom: '2px solid #eaeaea'
    },
    categoryGrid: {
      display: 'grid',
      gridTemplateColumns: 'repeat(auto-fill, minmax(280px, 1fr))',
      gap: '20px'
    },
    categoryItem: {
      borderRadius: '12px',
      padding: '20px',
      cursor: 'pointer',
      transition: 'all 0.3s ease',
      boxShadow: '0 4px 10px rgba(0, 0, 0, 0.08)',
      border: '1px solid #eaeaea',
      display: 'flex',
      alignItems: 'center',
      position: 'relative',
      overflow: 'hidden',
    },
    categoryIcon: {
      marginRight: '15px',
      width: '50px',
      height: '50px',
      borderRadius: '12px',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      fontSize: '24px',
      color: 'white',
      flexShrink: '0',
      boxShadow: '0 4px 8px rgba(0, 0, 0, 0.2)'
    },
    categoryName: {
      fontWeight: '600',
      color: '#2c3e50',
      fontSize: '18px'
    },
    notification: {
      position: 'fixed',
      top: '20px',
      right: '20px',
      background: 'linear-gradient(135deg, #27ae60, #2ecc71)',
      color: 'white',
      padding: '15px 25px',
      borderRadius: '8px',
      boxShadow: '0 5px 15px rgba(0, 0, 0, 0.2)',
      zIndex: '1000',
      fontWeight: '500'
    }
  };

  let gridTemplateColumns: string;

  if (isMobilePortrait) {
    gridTemplateColumns = 'repeat(2, 1fr)';
  } else if (isMobileLandscape) {
    gridTemplateColumns = 'repeat(3, 1fr)';
  } else {
    gridTemplateColumns = 'repeat(auto-fill, minmax(280px, 1fr))';
  }

  const categoryGridStyle = {
    ...styles.categoryGridBase,
    gridTemplateColumns: gridTemplateColumns
  };

  return (
    <div style={styles.container}>
      <h2 style={styles.heading}>Book Categories</h2>
    <div style={styles.categoryGrid}>
        {categories?.map((category,index) => (
          <div
            key={category.id}
            onClick={(e) => handleCategorySelect(e,category.id)}
            style={{
              ...styles.categoryItem,
              backgroundColor: index % 2 === 0 ? '#f8f9fa' : '#ffffff'
            }}
            className="category-item"
          >
            <div style={{
              ...styles.categoryIcon,
              backgroundColor: categoryColors[index] || '#3498db'
            }}>
              📚
            </div>
            <span style={styles.categoryName}>{category.name}</span>
            </div>            
        ))}
    </div>
    <style>
        {`
          .category-item:hover {
            transform: translateY(-5px);
            box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
            border-color: #3498db;
          }
        `}
      </style>
    </div>
  );
}

export default CategoryList;